const generateRandomLetter = (intro) => {
  intro.classList.add("appear");
  let intervals = 0;

  const interval = setInterval(() => {
    intro.innerText = intro.dataset.value
      .split("")
      .map((letter, i) => {
        if (i < intervals) return intro.dataset.value[i];

        return letters[Math.floor(Math.random() * letters.length)];
      })
      .join("");

    if (intervals >= intro.dataset.value.length) {
      clearInterval(interval);

      if (intro.dataset.value !== "JOMARI BORINES") {
        setTimeout(() => {
          intro.dataset.value = "JOMARI BORINES";
          setTimeout(() => {
            clickHoldBtn.classList.add("appear");
            generateRandomLetter(intro);
          }, 1000);
        }, 2000);
      }
    }
    intervals += 1 / 3;
  }, 50);

  PowerGlitch.glitch(".glitch", {
    playMode: "always",
    createContainers: true,
    hideOverflow: false,
    timing: {
      duration: 3000,
    },
    glitchTimeSpan: {
      start: 0.5,
      end: 0.7,
    },
    shake: {
      velocity: 15,
      amplitudeX: 0.2,
      amplitudeY: 0.2,
    },
    slice: {
      count: 4,
      velocity: 15,
      minHeight: 0.02,
      maxHeight: 0.15,
      hueRotate: true,
    },
    pulse: false,
  });
};

setTimeout(() => {
  let currentWidth = 0;
  const interval = setInterval(() => {
    if (currentWidth >= introBar.dataset.value) {
      clearInterval(interval);
      setTimeout(() => {
        intro.classList.add("appear");
        generateRandomLetter(intro);
      }, 1000);
    }

    currentWidth++;
    introBar.style.width = `${currentWidth}px`;
  }, 10);
}, 1500);

let holdTimeout;
let isHolding = false;

document.addEventListener("mousedown", () => {
  clickHoldBtn.classList.remove("appear");
  box.classList.add("loading");
  holdBtn.classList.add("appear");

  holdTimeout = setTimeout(() => {
    isHolding = true;
    introSection.style.display = "none";
    portfolio.style.display = "flex";
  }, 3020);
});

document.addEventListener("mouseup", () => {
  clearTimeout(holdTimeout);
  if (!isHolding) {
    clickHoldBtn.classList.add("appear");
    holdBtn.classList.remove("appear");
    box.classList.remove("loading");
  }
  isHolding = false;
});

const boxCopy = document.querySelectorAll(".boxCopy");
box.addEventListener("mouseenter", () => {
  boxCopy.forEach((box) => {
    box.style.transform = "rotate(0deg)";
  });
});

box.addEventListener("mouseleave", () => {
  let rotation = 0;
  boxCopy.forEach((box, index) => {
    rotation = (index + 1) * 20;
    box.style.transform = `rotate(${rotation}deg)`;
  });
});

let rotation = 0;
boxCopy.forEach((box, index) => {
  rotation = (index + 1) * 20;
  box.style.transform = `rotate(${rotation}deg)`;
});

